import Vue from "vue";
import Vuex from "vuex";
import axios from 'axios';
import API from '@/api';
Vue.use(Vuex);
// 创建vuex实例
const store = new Vuex.Store({
  state: {
    fruitData:[]
  },
  mutations: {
    saveFruitData(state, data) {
      state.fruitData = data
    }
  },
  actions: {
    delFruitData(context, id) {
      return new Promise((resolve, reject) => {
        API.delFruit(id).then(res => {
          console.log(res);
          resolve(res)
        })
      })
    },
    getFruitData({ commit }, pageInfo) {
      return new Promise((resolve, reject) => {
        API.getFruit(pageInfo).then(res => {
          commit('saveFruitData', res.result)
          resolve(res.pagingnation)
        })
      })
    }
  }
});
export default store;



