import axios from 'axios';
let base = process.env.API_ROOT;
axios.create({
  baseURL: base,
  timeout: 30000,
  headers: {
    'Content-Type': 'multipart/form-data',
    'Access-Control-Allow-Origin': '*'
  },
  withCredentials: true
});
// axios  respone拦截器
axios.interceptors.response.use(
  response => response,
  error => {
    console.log('err' + error)
    alert('操作失败')
    return Promise.reject(error)
  })
/**
 * 以下是所有接口配置，方便统一维护
 */
const API = {};
// 增加一种水果
API.addFruit = params => {
  return axios.post(`${base}/fruit`, params).then(res => res.data)
}
// 删除一种水果
API.delFruit = (id) => {
  return axios.delete(`${base}/fruit/${id}`).then(res => res.data)
}
// 获取所有水果信息（带分页）
API.getFruit = (params) => {
  return axios.get(`${base}/fruits`, {params}).then(res => res.data)
}
// 获取指定水果库存信息
API.getFruitById = (id) => {
  return axios.get(`${base}/fruit/${id}`).then(res => res.data)
}
export default API;



